import bg_form_seaweed from '../img/fondo_rojo_algas_derecha.svg';

export default function RegistrationForm() {

  const handleSubmit = (e) => {
    e.preventDefault()
  }
  return (
    <div className="section reg-section row" id="reg-section">
      <img src={bg_form_seaweed} className="bg-img" alt='seaweed background icon'/>
      <div className="col-xl-8 col-lg-9 col-md-10">
        <h2>Inscripción</h2>
        <h3>Ficha de inscripción</h3>
        <form onSubmit={(e) => handleSubmit(e)}>
          <div className="col-sm-6 col-12">
            <div className="form-field">
              <label className="col-12">Nombres*
                <input className="col-12" placeholder="" required/>
              </label>
            </div>
          </div>
          <div className="col-sm-6 col-12">
            <div className="form-field">
              <label className="col-12" >Apellidos*
                <input className="col-12" placeholder="" required/>
              </label>
            </div>
          </div>
          <div className="col-sm-6 col-12">
            <div className="form-field">
              <label className="col-12">Email*
                <input className="col-12" placeholder="" required/>
              </label>
            </div>
          </div>
          <div className="col-sm-6 col-12">
            <div className="form-field">
              <label className="col-12">Teléfono*
                <input className="col-12" placeholder="" required/>
              </label>
            </div>
          </div>
          <div className="col-sm-6 col-12">
            <div className="form-field">
              <label className="col-12">
              Nombre de la empresa / Institución*
                <input className="col-12" placeholder="" required/>
              </label>
            </div>
          </div>
          <div className="col-sm-6 col-12">
            <div className="form-field">
              <label className="col-12">Cargo*
                <input className="col-12" placeholder="" required/>
              </label>
            </div>
          </div>
          <div className="col-12">
            <div className="form-field">
              <label className="col-12">Asunto*
                <input className="col-12" placeholder="" required/>
              </label>
            </div>
          </div>
          <div className="col-12">
            <div className="form-field">
              <label className="col-12">Mensaje
                <textarea className="col-12" placeholder=""/>
              </label>
            </div>
          </div>
          <div>
            <input className="reg-btn" value="Enviar" type="submit"/>
          </div>
        </form>
      </div>
    </div>
  );
}
