import { useState } from 'react';
import Carousel  from 'react-bootstrap/Carousel';
import banner_lago_arapa from '../img/banner_lago_arapa.jpg'
import banner_langostino from '../img/banner_langostino.jpg'
import icon_footer_logo_cosechando from '../img/icon_footer_logo_cosechando.svg'
import icon_eventos_logo from '../img/icon_eventos_logo.svg'
import icon_cadena_trucha from '../img/icon_cadena_trucha.svg'
import icon_cadena_langostino from '../img/icon_cadena_langostino.svg'
import icon_cadena_pota from '../img/icon_cadena_pota.svg'
import icon_cadena_amazonicas from '../img/icon_cadena_amazonicas.svg'
import icon_cadena_macroalgas from '../img/icon_cadena_macroalgas.svg'
import icon_cadena_conchadeabanico from '../img/icon_cadena_conchadeabanico.svg'

export default function MainSlider() {
  const [index, setIndex] = useState(0);

  const handleSelect = (selectedIndex) => {
    setIndex(selectedIndex);
  };
  return (
    <Carousel activeIndex={index} onSelect={handleSelect}>
      <Carousel.Item>
        <div className='first-slide' style={{ backgroundImage:`url(${banner_lago_arapa})` }}>
          <div>
            <img className='slide-slogan' src={icon_footer_logo_cosechando} alt="logo cosechando"></img>
          </div>
          <div>
            <div className='slide-top-text'>+ de 1900</div>
            <div className='slide-red-text'>proyectos de innovación</div>
            <div className='slide-bottom-text'>valorizados en + de 300 millones de soles.</div>
          </div>
        </div>
      </Carousel.Item>
      <Carousel.Item>
        <div className='second-slide' style={{ backgroundImage:`url(${banner_langostino})` }}>
          <div>
            <img className='slide-slogan' src={icon_eventos_logo} alt="event logo"></img>
          </div>
          <div className='slide-title'>- Cadenas de valor -</div>
          <div className='slide-icons'>
            <div>
              <img className='slide-icon' src={icon_cadena_trucha} alt="icon trucha"/>
              <img className='slide-icon' src={icon_cadena_langostino} alt="icon langostino"/>
              <img className='slide-icon' src={icon_cadena_pota} alt="icon pota"/>
            </div>
            <div>
              <img className='slide-icon' src={icon_cadena_amazonicas} alt="icon paiche"/>
              <img className='slide-icon' src={icon_cadena_macroalgas} alt="icon macroalgas"/>
              <img className='slide-icon' src={icon_cadena_conchadeabanico} alt="icon concha"/>
            </div>

          </div>
        </div>
      </Carousel.Item>
    </Carousel>
  );
}

